.layer {
  background-color: rgba(31, 34, 37, 0.9);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

section.intro {
  background: #1f2225 url("/img/backgrounds/home/door.jpg") no-repeat center;
  position: relative;
  height: 450px;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  border-bottom: 1px solid #4c4e51;
}

section.intro .section-content {
  margin: 0 auto;
  position: relative;
}

section.intro h1.heading {
  font-size: 40px;
  color: white;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 20px;
}

section.tasks {
  background: #090c0e url("../img/circle.png") no-repeat center;
  height: 450px;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
}

section.tasks .section-content {
  margin: 0 auto;
}

section.tasks .key {
  margin-left: 10px;
}

section.tasks .text-left {
  text-align: left;
}

section.tasks .text-right {
  text-align: right;
}

section.tasks .left-side {
  width: 43%;
  float: left;
}

@media screen and (max-width: 700px) {
  section.tasks .left-side {
    width: 100%;
  }
}

section.tasks .right-side {
  width: 43%;
  float: left;
}

@media screen and (max-width: 700px) {
  section.tasks .right-side {
    width: 100%;
  }
}

section.tasks .mid {
  width: 14%;
  float: left;
}

@media screen and (max-width: 700px) {
  section.tasks .mid {
    width: 100%;
  }
  section.tasks .mid img {
    height: 100px;
  }
}

section.tasks .task-container .heading {
  color: white;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}

section.tasks .task-container .task-number {
  color: #fb383c;
}

section.tasks .task-container .desc {
  color: #6c7176;
  text-align: justify;
  width: 70%;
}

@media screen and (max-width: 700px) {
  section.tasks .task-container .desc {
    width: 100%;
    text-align: center;
  }
}

section.tasks .task-container .l {
  margin-left: 30%;
}

@media screen and (max-width: 700px) {
  section.tasks .task-container .l {
    margin-left: 0;
  }
}

section.tasks .task-container .r {
  margin-right: 30%;
}

@media screen and (max-width: 700px) {
  section.tasks .task-container .r {
    margin-right: 0;
  }
}

section.tasks .task-container p {
  margin: 15px 0 15px 0;
  font-family: "Open Sans", sans-serif;
  font-size: 13px;
}

@media screen and (max-width: 700px) {
  section.tasks .task-container p {
    margin: 10px 0 10px 0;
    text-align: center;
  }
}

section.statistics {
  display: flex;
  flex-wrap: wrap;
}

section.statistics .wrap {
  width: 100%;
  text-align: center;
  background-color: #1f2225;
}

section.statistics .wrap .count {
  color: white;
  font-size: 30px;
}

section.statistics .wrap .desc {
  color: #6c7176;
  font-size: 16px;
}

section.statistics .wrap p {
  display: inline;
}

section.statistics .wrap .content {
  padding: 20px 0 20px 0;
}

section.statistics .wrap .bordered {
  box-sizing: border-box;
  border-left: 1px solid #4c4e51;
  border-right: 1px solid #4c4e51;
}
